import React, { useEffect, useState } from 'react';
import EntityContainer from '../../../../../modules/entity/containers';
import { convertToTreeSelectData } from '../../../utils/convertsTreeSelectData';
import TreeSelect, { SHOW_PARENT } from 'rc-tree-select';
import { Loader } from '../../../../../shared/components';
import { useAppState } from '../../../../../shared/state';
import { getDepartmentIds } from '../../../utils/getDepartmentIds';

export default function StaffGeneralTreeSelect({ id, departments }: any) {
	const { companyId } = useAppState();
	let treeDataStaff = [];
	let treeDataClient = [];

	const [deps, setDeps] = useState<any>(null);

	useEffect(() => {
		if (departments) {
			setDeps(getDepartmentIds(departments));
		}
	}, [departments]);
	return (
		<div className="flex w-full flex-wrap">
			<div className="mb-5 mr-0 w-full lg:mb-0 lg:mr-3 lg:w-5/12">
				<div className="mb-1.5 pl-1.5 text-sm text-kdark dark:text-white">Staff Departments</div>
				{id && companyId ? (
					<EntityContainer.All
						entity="tree"
						name={`AllTreeDepartments-Staff-${id}`}
						url={`/${companyId}/departments/tree/`}
						params={{
							limit: 100,
							extra: {
								is_staff: 'True'
							}
						}}
					>
						{({ items, isFetched: isFetchedClientsDeps }) => {
							treeDataStaff = isFetchedClientsDeps && convertToTreeSelectData(items);
							return (
								<>
									{isFetchedClientsDeps ? (
										<TreeSelect
											getPopupContainer={(triggerNode: any) => triggerNode.parentNode}
											className="check-select pointer-events-none w-full"
											transitionName="rc-tree-select-dropdown-slide-up"
											choiceTransitionName="rc-tree-select-selection__choice-zoom"
											dropdownStyle={{ overflow: 'auto' }}
											dropdownPopupAlign={{
												overflow: { adjustY: 0, adjustX: 0 },
												offset: [0, 2]
											}}
											placeholder={<i>Select Client Departments</i>}
											searchPlaceholder="please search"
											treeLine
											maxTagTextLength={10}
											multiple
											allowClear={false}
											autoClearSearchValue
											treeData={treeDataStaff}
											treeNodeFilterProp="title"
											treeCheckable
											showCheckedStrategy={SHOW_PARENT}
											value={deps?.staffDeps}
										/>
									) : (
										<div className="flex justify-center">
											<Loader />
										</div>
									)}
								</>
							);
						}}
					</EntityContainer.All>
				) : null}
			</div>
			<div className="w-full lg:w-6/12">
				<div className="mb-1.5 pl-1.5 text-sm text-kdark dark:text-white">Client Departments</div>
				{id && companyId ? (
					<EntityContainer.All
						entity="tree"
						name={`AllTreeDepartments-Client-${id}`}
						url={`/${companyId}/departments/tree/`}
						params={{
							limit: 100,
							extra: {
								is_staff: 'False'
							}
						}}
					>
						{({ items, isFetched: isFetchedClientsDeps }) => {
							treeDataClient = isFetchedClientsDeps && convertToTreeSelectData(items);
							return (
								<>
									{isFetchedClientsDeps ? (
										<TreeSelect
											getPopupContainer={(triggerNode: any) => triggerNode.parentNode}
											className="check-select pointer-events-none w-full"
											transitionName="rc-tree-select-dropdown-slide-up"
											choiceTransitionName="rc-tree-select-selection__choice-zoom"
											dropdownStyle={{ overflow: 'auto' }}
											dropdownPopupAlign={{
												overflow: { adjustY: 0, adjustX: 0 },
												offset: [0, 2]
											}}
											placeholder={<i>Select Client Departments</i>}
											searchPlaceholder="please search"
											treeLine
											maxTagTextLength={10}
											multiple
											allowClear={false}
											autoClearSearchValue
											treeData={treeDataClient}
											treeNodeFilterProp="title"
											treeCheckable
											showCheckedStrategy={SHOW_PARENT}
											value={deps?.clientDeps}
										/>
									) : (
										<div className="flex justify-center">
											<Loader />
										</div>
									)}
								</>
							);
						}}
					</EntityContainer.All>
				) : null}
			</div>
		</div>
	);
}
