import React from 'react';
import { IconClientUsers } from '../../../../../shared/icons';
import { DetailsInfo } from '../../../../../shared/components/Elements';
import { HiHome } from 'react-icons/hi';
import { FaUserCog, FaUsers } from 'react-icons/fa';
import { Avatar } from '@material-ui/core';
import { Loader } from '../../../../../shared/components';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import { useParams } from 'react-router';
import StaffGeneralTreeSelect from './StaffGeneralTreeSelect';
import { StaffManagementItemRequest } from 'shared/typings/staffManagement.types';

export default function StaffDetailsGeneral({ setModalItem }: any) {
	const { id } = useParams<any>();
	const { companyId } = useAppState();
	return (
		<>
			{companyId ? (
				<EntityContainer.One
					primaryKey="id"
					id={id}
					entity="staffEmployee"
					name={`staffEmployee-${companyId}`}
					url={`${companyId}/employee/${id}/`}
					params={{}}
					relations={''}
				>
					{({ item, isFetched }) => {
						const detailsItem = item as StaffManagementItemRequest;
						const {
							avatar,
							first_name,
							mobile,
							last_name,
							username,
							roles,
							id,
							departments,
							is_business_owner
						} = detailsItem;
						setModalItem(item);
						const staffRole = isFetched ? `${roles[0]} ${is_business_owner ? '| owner' : ''}` : '';
						return (
							<>
								{isFetched ? (
									<div className="mb-16 flex flex-col pt-8 lg:flex-row">
										<div className="mb-5 mr-12 h-28 w-28 flex-shrink-0 overflow-hidden rounded-full bg-kgrey-bg lg:mb-0">
											<Avatar
												className="inline-block"
												style={{ color: '#fff', width: '100%', height: '100%' }}
												src={avatar?.url}
											>
												{first_name[0]}
												{last_name[0]}
											</Avatar>
										</div>
										<div className="flex-grow">
											<div className="flex flex-wrap">
												<div className="mb-3">
													<div className="mb-3 pl-5 text-xl font-medium text-kdark dark:text-white">
														Personal Information
													</div>
													<DetailsInfo
														text={first_name}
														wrapperClassnames="bg-kgrey-bgmenu shadow-recent-panel-2"
														title="First name"
														textClassName="!text-lg font-semibold"
													>
														<HiHome className="text-kgrey" />
													</DetailsInfo>
													<DetailsInfo
														text={last_name}
														title="Last name"
														wrapperClassnames="bg-kgrey-bgmenu shadow-recent-panel-2"
														textClassName="text-lg font-semibold"
													>
														<FaUserCog className="text-kgrey" />
													</DetailsInfo>
												</div>

												<div className="mb-3">
													<div className="mb-3 pl-5 text-xl font-medium text-kdark dark:text-white">
														Contact Information
													</div>
													<DetailsInfo
														text={username}
														title="Email"
														wrapperClassnames="bg-kgrey-bgmenu shadow-recent-panel-2"
														textClassName="!text-lg !font-med"
													>
														<IconClientUsers className="text-kgrey" />
													</DetailsInfo>
													{mobile ? (
														<DetailsInfo
															text={mobile}
															title="Mobile Phone"
															wrapperClassnames="bg-kgrey-bgmenu shadow-recent-panel-2"
															textClassName="!text-lg font-semibold"
														>
															<FaUsers className="text-kgrey" />
														</DetailsInfo>
													) : null}
												</div>
											</div>
											<div>
												<div className="mb-3 pl-5 text-xl font-medium text-kdark dark:text-white">
													Additional Information
												</div>
												<div className="mb-2 inline-block">
													<DetailsInfo
														text={staffRole}
														title="Role"
														wrapperClassnames="bg-kgrey-bgmenu shadow-recent-panel-2"
														textClassName="!text-lg font-semibold"
													>
														<FaUsers className="text-kgrey" />
													</DetailsInfo>
												</div>
												<div className="w-full">
													<StaffGeneralTreeSelect departments={departments} id={id} />
												</div>
											</div>
										</div>
									</div>
								) : (
									<div className="flex justify-center">
										<Loader />
									</div>
								)}
							</>
						);
					}}
				</EntityContainer.One>
			) : null}
		</>
	);
}
