export function getDepartmentIds(departments: any) {
	return departments
		? departments.reduce(
				(acc: any, curr: any) => {
					if (!curr.is_staff) {
						return {
							...acc,
							clientDeps: [...acc.clientDeps, curr.id]
						};
					} else {
						return {
							...acc,
							staffDeps: [...acc.staffDeps, curr.id]
						};
					}
				},
				{ clientDeps: [], staffDeps: [] }
		  )
		: [];
}
